import React, { Fragment, useState, useEffect } from 'react';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  createProfile,
  getCurrentProfile,
  deleteAccount
} from '../../actions/profile';

const EditProfile = ({
  profile: { profile, loading },
  createProfile,
  getCurrentProfile,
  history,
  deleteAccount,
}) => {
  const [formData, setFormData] = useState({
    bio: '',
    twitter: '',
    youtube: '',
    instagram: '',
    pc: '',
    xbox: '',
    playstation: ''
  });

  const [displaySocialInputs, toggleSocialInputs] = useState(false);

  useEffect(() => {
    getCurrentProfile();

    setFormData({
      bio: loading || !profile.bio ? '' : profile.bio,
      twitter: loading || !profile.social ? '' : profile.social.twitter,
      youtube: loading || !profile.social ? '' : profile.social.youtube,
      instagram: loading || !profile.social ? '' : profile.social.instagram,
      pc: loading || !profile.gamertags ? '' : profile.gamertags.pc,
      xbox: loading || !profile.gamertags ? '' : profile.gamertags.xbox,
      playstation:
        loading || !profile.gamertags ? '' : profile.gamertags.playstation
    });
  }, [loading, getCurrentProfile]);

  const { bio, twitter, youtube, instagram, pc, xbox, playstation } = formData;

  const onChange = e =>
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });

  const onSubmit = e => {
    e.preventDefault();
    createProfile(formData, history, true);
  };

  return (
    <Fragment>
      <h1 className='large text-primaryy'> Edit Your Profile </h1>{' '}
      <p className='lead'>
        <i className='fas fa-user' /> Add some changes to your profile{' '}
      </p>{' '}
      <small> * = required field </small>{' '}
      <form className='form' onSubmit={e => onSubmit(e)}>
        <div className='form-group'>
          <textarea
            placeholder='A short bio of yourself'
            name='bio'
            value={bio}
            onChange={e => onChange(e)}
          />{' '}
          <small className='form-text'> Tell us a little about yourself </small>{' '}
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='PC GT'
            name='pc'
            value={pc}
            onChange={onChange}
          />
          <small className='form-text'>Enter your PC GamerTag</small>
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='XBOX GT'
            name='xbox'
            value={xbox}
            onChange={onChange}
          />
          <small className='form-text'>Enter your Xbox GamerTag</small>
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Playstation GT'
            name='playstation'
            value={playstation}
            onChange={onChange}
          />
          <small className='form-text'>Enter your Playstation GamerTag</small>
        </div>
        <div className='my-2'>
          <button
            onClick={() => toggleSocialInputs(!displaySocialInputs)}
            type='button'
            className='btn btn-light'
          >
            Add Social Network Links{' '}
          </button>{' '}
          <span> Optional </span>{' '}
        </div>
        {displaySocialInputs && (
          <Fragment>
            <div className='form-group social-input'>
              <i className='fab fa-twitter fa-2x' />
              <input
                type='text'
                placeholder='Twitter URL'
                name='twitter'
                value={twitter}
                onChange={e => onChange(e)}
              />{' '}
            </div>

            <div className='form-group social-input'>
              <i className='fab fa-youtube fa-2x' />
              <input
                type='text'
                placeholder='YouTube URL'
                name='youtube'
                value={youtube}
                onChange={e => onChange(e)}
              />{' '}
            </div>

            <div className='form-group social-input'>
              <i className='fab fa-instagram fa-2x' />
              <input
                type='text'
                placeholder='Instagram URL'
                name='instagram'
                value={instagram}
                onChange={e => onChange(e)}
              />{' '}
            </div>
          </Fragment>
        )}{' '}
        <input type='submit' className='btn btn-primary my-1' />
        <Link className='btn btn-light my-1' to='/dashboard'>
          Go Back{' '}
        </Link>{' '}
        <Fragment>
          <div className='my-2'>
            <button className='btn btn-danger' onClick={() => deleteAccount()}>
              <i className='fas fa-user-minus' /> Delete My Account{' '}
            </button>{' '}
          </div>{' '}
        </Fragment>{' '}
      </form>{' '}
    </Fragment>
  );
};

EditProfile.propTypes = {
  createProfile: PropTypes.func.isRequired,
  deleteAccount: PropTypes.func.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  profile: state.profile
});

export default connect(mapStateToProps, {
  createProfile,
  getCurrentProfile,
  deleteAccount,
})(withRouter(EditProfile));
