import React, { Fragment, useEffect } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Spinner from '../layout/Spinner';
import DashboardActions from './DashboardActions';
import { accept, decline, won, lost, getCurrentWager, cancel, cancelPublic, getDisputedWagers } from '../../actions/wager';
import { setAlert } from '../../actions/alert';
import { getCurrentProfile} from '../../actions/profile';
import { getWagers } from '../../actions/wager';
import Wagers from './Wagers';
import OnGoingWager from './OnGoingWager';

const Dashboard = ({
  won,
  lost,
  cancel,
  cancelPublic,
  accept,
  decline,
  getWagers,
   getCurrentWager,
   getCurrentProfile,
   getDisputedWagers,
  profile: { profile },
  auth: { user, loading },
  wager
}) => {
  useEffect(() => {
    (async function anyNameFunction() {
     await getDisputedWagers();
    })()
    getCurrentWager();
    getCurrentProfile();
    getWagers();
  }, [ getCurrentProfile, getWagers, getCurrentWager, getDisputedWagers]);

  return loading && user === null ? (
    <Spinner />
  ) : (
   
    <Fragment>
      <h1 className='large text-primaryy'>Dashboard</h1>
      <p className='lead'>
        <i className='fas fa-user' /> Welcome {user && user.username}
      </p>
      {profile !== null ? (
        <Fragment>
          <DashboardActions wager ={wager} />
         
            
          {wager.currentwager !== null ? (
            <Fragment>
              <OnGoingWager wager={wager} won={won} lost={lost} cancel={cancel} />
            </Fragment>
          ) : (
            <Fragment>NO ONGOING WAGERS AT THIS TIME</Fragment>
          )}
            
      
     
          <Wagers wager={wager} user={user} accept={accept} decline={decline} cancelPublic={cancelPublic} />
          </Fragment>
          ) : (
        <Fragment>
          <p>You have not yet setup a profile, please add some info</p>
          <Link to="/create-profile" className="btn btn-primary my-1">
            Create Profile
          </Link>
    
        </Fragment>
      )}
    </Fragment>
  );
};

Dashboard.propTypes = {
  getCurrentProfile: PropTypes.func.isRequired,
  deleteAccount: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  profile: PropTypes.object.isRequired,
  wager: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  profile: state.profile,
  wager: state.wager,
});

export default connect(mapStateToProps, {
  won,
  lost,
  cancel,
  accept,
  decline,
  getCurrentProfile,
cancelPublic,
  // deleteAccount,
  getWagers,
  getCurrentWager,
  getDisputedWagers,
})(Dashboard);
