import React from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

const Landing = ({ isAuthenticated }) => {
  if (isAuthenticated) {
    return <Redirect to='/dashboard' />;
  }

  return (
    <section className='landing'>
      <div className='dark-overlay'>
        <div className='landing-inner'>
          <h1 className='x-large'>VG Wagers</h1>
          <p className='lead'>Dedicated Wager system for the Gamers</p>
          <div className='buttons'>
            <Link to='/register' className='btn btn-primary'>
              Sign Up
            </Link>
            <Link to='/login' className='btn btn-light'>
              Login
            </Link>
          </div>
        </div>
        <p className='coming-soon'>COMING SOON..</p>
        <div className='landing-under'>
          <div class='ribbon'>
            <h3>Team Wagers</h3>
            <p>
              This will allow users to squad up with other users to wager
              against other team in any game.
            </p>
          </div>
          <div class='ribbon'>
            <h3>Player Reviews</h3>
            <p>
              This will allows users to rate the players they play against to
              provide a more trustworthy experience
            </p>
          </div>
          <div class='ribbon'>
            <h3>Multiple Countries</h3>
            <p>
              So far this application is only accessible for users in the USA
              but we want everyone around the world to be able to access this
              site
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};

Landing.propTypes = {
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps)(Landing);
