import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Moment from 'react-moment';
import moment from 'moment';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { connect } from 'react-redux';

const Wagers = ({ wager: { wagers }, user, accept, decline, cancelPublic }) => {
  const onClickAccept = (wg) => {
    const id = wg._id;
    console.log(id);
    accept({ id });
    // accept();
  };
  const onClickDecline = (wg) => {
    const id = wg._id;
    console.log(id);
    decline({ id });
  };

  const onClickCancel = (wg) => {
    const id = wg._id;
    cancelPublic({ id });
  };

  const allTheWagers = wagers.map((wg) => (
    <Tr key={wg._id}>
      <Td className='hide-sm'>{wg.challenger}</Td>
      <Td className='hide-sm'>{wg.challengee}</Td>
      <Td className='hide-sm'>{wg.game}</Td>
      <Td className='hide-sm'>{wg.amount}</Td>
      <Td className='hide-sm'>{wg.description}</Td>
      <Td className='hide-sm'>{moment(wg.date).format('MMMM Do, h:mm a')}</Td>
      {user.username === wg.challengee ? (
        <div>
          <Td className='hide-sm'>
            <input
              type='image'
              src={require('../../img/ok.png')}
              className='greencheck'
              onClick={() => onClickAccept(wg)}
            />
          </Td>
          <Td className='hide-sm'>
            <input
              type='image'
              src={require('../../img/redx.jpg')}
              className='redx'
              onClick={() => onClickDecline(wg)}
            />
          </Td>
        </div>
      ) : (
        <Td className='hide-sm'></Td>
      )}
      {user.username === wg.challenger && wg.status === false ? (
        <Td className='hide-sm'>
          <input
            type='button'
            value='Cancel Wager'
            className='cancelwager'
            onClick={() => onClickCancel(wg)}
          />
        </Td>
      ) : (
        <Td className='hide-sm'>Already Accepted</Td>
      )}
    </Tr>
  ));

  return (
    <Fragment>
      <h2 className='my-2' style={{ color: '#4f8737' }}>
        Wagers
      </h2>
      <div className='table-responsive '>
        <Table className='table table-dark table-bordered '>
          <Thead>
            <Tr>
              <Th className='hide-sm'>Challenger</Th>
              <Th className='hide-sm'>Challengee</Th>
              <Th className='hide-sm'>Game</Th>
              <Th className='hide-sm'>Amount</Th>
              <Th className='hide-sm'>Description</Th>
              <Th className='hide-sm'>Date</Th>
              <Th className='hide-sm'>Accept/Decline</Th>
              <Th className='hide-sm'>Cancel</Th>
            </Tr>
          </Thead>
          <Tbody>{allTheWagers}</Tbody>
        </Table>
      </div>
    </Fragment>
  );
};

Wagers.propTypes = {
  wagers: PropTypes.array.isRequired,
};

export default Wagers;
