//GET_DISPUTEDWAGER, is to get all disputed wagers

import {
  GET_WAGER,
  GET_WAGERS,
  DELETE_WAGER,
  WAGER_ERROR,
  DELETE_FINSHEDWAGER,
  GET_COMPLETEDWAGER,
  GET_DISPUTEDWAGER,
    GET_CURRENTWAGER,
    GET_PUBLICWAGERS,
    CANCEL_WAGER,
    GET_DISPUTEFORMWAGER,
    DISPUTEFORM_STATUS,
    ACCEPT_PUBLICWAGER,
    CANCEL_PUBLICWAGER,
  //  CREATE_WAGER
} from '../actions/types';

const initialState = {
  wagers: [],
  currentwager: [],
  loading: true,
  error: {},
  disputedwagers:[],
  disputedwager:null
  
};

export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    //get request
    case GET_WAGERS:
    case GET_PUBLICWAGERS:
      return {
        ...state,
        wagers: payload,
        loading: false,
      };
    // accepts(post)
    case GET_WAGER:
      return {
        ...state,
        currentwager: [payload],
        wagers: state.wagers.filter((wager) => wager._id !== payload._id),
        loading: false,
      };
    case GET_COMPLETEDWAGER:
        return{
          ...state,
          currentwager: null,
          wagers: payload,
          loading:false
        };
    case GET_DISPUTEDWAGER:
    case DISPUTEFORM_STATUS:
          return{
            ...state,
           
            disputedwagers: payload,
            loading:false
          };
    case GET_DISPUTEFORMWAGER:
          return{
            ...state,
            disputedwager: payload,
          }
    // case SET_DISPUTEFORMWAGER:
    //   return{
    //     ...state,
    //   }

      //get request
    case GET_CURRENTWAGER:
      return {
        ...state,
        currentwager: payload,
        loading: false,
      };
      case CANCEL_WAGER:
        return{
          ...state,
        currentwager:state.currentwager.filter((wager) => wager._id !== payload._id),
        loading: false,
        };
        case CANCEL_PUBLICWAGER:
          return{
            ...state,
            wagers:state.wagers.filter((wager) => wager._id !== payload._id),
            loading: false,
          };
    // case CREATE_WAGER:
    //   return {
    //     ...state,
    //     currentwager:null,
    //     wagers: [payload, ...state.wagers],
    //     loading: false
    //   };
    case DELETE_WAGER:
      return {
        ...state,
        wagers: state.wagers.filter((wager) => wager._id !== payload._id),
        loading: false,
      };
    case DELETE_FINSHEDWAGER:
      return {
        ...state,
        currentwager: null,
        loading: false,
      };
    case ACCEPT_PUBLICWAGER:
      return {
        ...state,
        wagers: state.wagers.filter((wager) => wager._id !== payload._id),
        loading: false,
      }
    case WAGER_ERROR:
      return {
        ...state,
        error: payload,
        loading: false,
      };

    default:
      return state;
  }
}
