import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import {
  getCompletedWagers,
  getDisputedWagers,
  getDisputedFormWager,
} from '../../actions/wager';
import Spinner from '../layout/Spinner';
import moment from 'moment';
import { loadUser } from '../../actions/auth';

const PreviousWagers = ({
  getCompletedWagers,
  getDisputedWagers,
  getDisputedFormWager,
  loadUser,
  wager: { wagers, disputedwagers },
  auth: { user },
}) => {
  useEffect(() => {
    (async function anyNameFunction() {
      await loadUser();
      getCompletedWagers();
      getDisputedWagers();
    })();
  }, [getCompletedWagers, getDisputedWagers]);

  const onSetDisputedFormWager = (wg) => {
    const wager = wg;
    getDisputedFormWager(wager);
  };

  const disputedWagers = disputedwagers.map((wg) => (
    <Tr key={wg._id}>
      <Td className='hide-sm'>{wg.challenger}</Td>
      <Td className='hide-sm'>{wg.challengee}</Td>
      <Td className='hide-sm'>{wg.game}</Td>
      <Td className='hide-sm'>{wg.amount}</Td>
      <Td className='hide-sm'>{wg.description}</Td>
      <Td className='hide-sm'>{moment(wg.date).format('MMMM Do, h:mm a')}</Td>
      {/* check to see if user is challengee or challenger save That as a const */}
      {(user.username === wg.challengee) &
        (wg.challengeeDisputeForm.status === true) ||
      (user.username === wg.challenger) &
        (wg.challengerDisputeForm.status === true) ? (
        <Td className='hide-sm'>Waiting For Decision</Td>
      ) : (
        <Td className='hide-sm'>
          <Link to='/dispute-form'>
            <input
              type='button'
              value='form'
              className='form'
              onClick={() => onSetDisputedFormWager(wg)}
            />
          </Link>
        </Td>
      )}
    </Tr>
  ));
  // map Thru all wagers That is set for a specific user wager:{wagers} object state"
  const allTheWagers = wagers.map((wg) => (
    <Tr key={wg._id}>
      <Td className='hide-sm'>{wg.challenger}</Td>
      <Td className='hide-sm'>{wg.challengee}</Td>
      <Td className='hide-sm'>{wg.game}</Td>
      <Td className='hide-sm'>{wg.amount}</Td>
      <Td className='hide-sm'>{wg.description}</Td>
      <Td className='hide-sm'>{moment(wg.date).format('MMMM Do, h:mm a')}</Td>
      {(user.username === wg.challengee) & (wg.decision.challengee === 'won') ||
      (user.username === wg.challenger) & (wg.decision.challenger === 'won') ? (
        <Td className='hide-sm'>
          <input
            type='image'
            src={require('../../img/ok.png')}
            className='greencheck'
          />
        </Td>
      ) : (
        <Td className='hide-sm'>
          <input
            type='image'
            src={require('../../img/redx.jpg')}
            className='redx'
          />
        </Td>
      )}
    </Tr>
  ));

  return user === null ? (
    <Spinner />
  ) : (
    <div>
      <Fragment>
        <h2 className='my-2' style={{ color: '#4f8737' }}>
          Disputed Wagers
        </h2>
        <div className='table-responsive'>
          <Table className='table table-dark table-bordered'>
            <Thead>
              <Tr>
                <Th className='hide-sm'>Challenger</Th>
                <Th className='hide-sm'>Challengee</Th>
                <Th className='hide-sm'>Game</Th>
                <Th className='hide-sm'>Amount</Th>
                <Th className='hide-sm'>Description</Th>
                <Th className='hide-sm'>Date</Th>
                <Th className='hide-sm'>DisputeForm</Th>
              </Tr>
            </Thead>
            <Tbody>{disputedWagers}</Tbody>
          </Table>
        </div>
      </Fragment>

      <Fragment>
        <h2 className='my-2' style={{ color: '#4f8737' }}>
          Previous Wagers
        </h2>

        <div className='table-responsive'>
          <Table className='table table-dark table-bordered'>
            <Thead>
              <Tr>
                <Th className='hide-sm'>Challenger</Th>
                <Th className='hide-sm'>Challengee</Th>
                <Th className='hide-sm'>Game</Th>
                <Th className='hide-sm'>Amount</Th>
                <Th className='hide-sm'>Description</Th>
                <Th className='hide-sm'>Date</Th>
                <Th className='hide-sm'>Outcome</Th>
              </Tr>
            </Thead>
            <tbody>{allTheWagers}</tbody>
          </Table>
        </div>
      </Fragment>
    </div>
  );
};
//check typing dont need if we were static typing like typescript
PreviousWagers.propTypes = {
  wagers: PropTypes.array.isRequired,
};
const mapStateToProps = (state) => ({
  wager: state.wager,
  auth: state.auth,
});

// getting actions from (actions/wager import) using connect, map state to props is getting The state and mapping it to a prop
export default connect(mapStateToProps, {
  getCompletedWagers,
  getDisputedWagers,
  getDisputedFormWager,
  loadUser,
})(PreviousWagers);
