import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import moment from 'moment';

//find out why current wager isnt being set
const OnGoingWager = ({ wager: { currentwager }, won, lost, cancel }) => {
 
  const onClickWon = (wg) => {
    const id = wg._id;
    console.log(id);
    won({ id });
  };
  const onClickLost = (wg) => {
    const id = wg._id;
    console.log(id);
    lost({ id });
  };
  const onClickCancel = (wg) => {
    const id = wg._id;
    cancel({ id });
  };

  const wagerOngoing = currentwager.map((wg) => (
    <Tr key={wg._id}>
      <Td className='hide-sm'>{wg.challenger}</Td>
      <Td className='hide-sm'>{wg.challengee}</Td>
      <Td className='hide-sm'>{wg.game}</Td>
      <Td className='hide-sm'>{wg.amount}</Td>
      <Td className='hide-sm'>{wg.description}</Td>
      <Td className='hide-sm'>{moment(wg.date).format('MMMM Do, h:mm a')}</Td>
      {/* condition to show i won i lost button if wager status is set true */}
      {wg.status === true ? (
        
        <Td className='hide-sm'>
          <input
            type='button'
            value='I WON'
            className='wonbtn'
            onClick={() => onClickWon(wg)}
          />
           
          <input
            type='button'
            value='I LOST'
            className='lostbtn'
            onClick={() => onClickLost(wg)}
          />
       
        </Td>
      ) : (
        <Td className='hide-sm'>not accepted</Td>
      )}
      {wg.status === true ? (
        <Td className='hide-sm'>
          <input
            type='image'
            src={require('../../img/ok.png')}
            className='acceptedwager'
          />
        </Td>
      ) : (
        <Td className='hide-sm'>not accepted</Td>
      )}
      {wg.status === false ? (
        <Td className='hide-sm'>
          <input
            type='button'
            value='Cancel Wager'
            className='cancelwager'
            onClick={() => onClickCancel(wg)}
          />
        </Td>
      ) : (
        <Td className='hide-sm'>Already Accepted</Td>
      )}
    </Tr>
  ));
  return (
    <Fragment>
      <h2 className='my-2' style={{ color: '#4f8737' }}>
        On Going Wager
      </h2>
      <div className='table-responsive'>
        <Table className='table table-dark table-bordered'>
          <Thead>
            <Tr>
              <Th className='hide-sm'>Challenger</Th>
              <Th className='hide-sm'>Challengee</Th>
              <Th className='hide-sm'>Game</Th>
              <Th className='hide-sm'>Amount</Th>
              <Th className='hide-sm'>Description</Th>
              <Th className='hide-sm'>Date</Th>
              <Th className='hide-sm'>Decision</Th>
              <Th className='hide-sm'>Accepted</Th>
              <Th className='hide-sm'>Cancel</Th>
            </Tr>
          </Thead>
          <Tbody>{wagerOngoing}</Tbody>
        </Table>
      </div>
    </Fragment>
  );
};

OnGoingWager.propTypes = {};

export default OnGoingWager;
