import React, {  useState, Fragment } from 'react';
import PropTypes from 'prop-types';
// import { getCurrentProfile } from '../../actions/profile';
// import { withRouter, Redirect } from 'react-router-dom';
import { createPublicWager } from '../../actions/wager';
import { setAlert } from '../../actions/alert';
import { connect } from 'react-redux';

// import SearchUser from './SearchUser';

//games

const PublicWager = ({ createPublicWager, auth: { user } }) => {


  const [createTheWager, setCreateTheWager] = useState({
    game: '',
    description: '',
    amount: ''
  });


  const { game, description, amount } = createTheWager;

  const onChange = e =>
    setCreateTheWager({ ...createTheWager, [e.target.name]: e.target.value });

  const onSubmit = e => {
    e.preventDefault();
    alert('$1.00 service fee for creating the wager, it will be refunded if the wager is not accepted or the wager gets canceled');
    createPublicWager(createTheWager);
    console.log(createTheWager);
  };



 

  return (
    <Fragment>
                <h1 className="customwagertitle">Create A Custom Wager</h1>
                 
    <form className='form' onSubmit={e => onSubmit(e)}>                 
 <div className='form-group'>
 <div className='challenge-game bg-white p-2'>
 <h2 className='text-primaryy'>GAME:</h2>
 <textarea
              rows='1'
              cols='50'
              placeholder='Game Name'
              name='game'
              value={game}
              onChange={e => onChange(e)}
            ></textarea>
          </div>
 </div>

        <div className='form-group'>
          <div className='challenge-description bg-white p-2'>
            <h2 className='text-primaryy'>Wager Description</h2>

            <textarea
              rows='4'
              cols='100'
              placeholder='Description of the Wager'
              name='description'
              value={description}
              onChange={e => onChange(e)}
            ></textarea>
          </div>
 </div>

        <div className='form-group'>
          <div className='challenge-price bg-white p-2'>
            <h2 className='text-primaryy'>Wager Price</h2>
            <h3>for each player:</h3>
            <input
              type='number'
              placeholder='$0.00'
              min='0.00'
              max='300.00'
              name='amount'
              value={amount}
              onChange={e => onChange(e)}
            />
            <h4>min: $1.00 max: $300.00</h4>
          </div>
 </div>
        <input type='submit' className='btn btn-primary my-1' />
      </form>
    </Fragment>
  );
};

//on change

// on submit

// fields
PublicWager.propTypes = {
  createWager: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  wager: state.wager,
 
});

export default connect(mapStateToProps, { createPublicWager })(PublicWager);
