export const createWager = (createTheWager) => async (dispatch) => {
 
  try {
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };
  
    const res = await axios.post('/api/wager', createTheWager, config);

    dispatch({
      type: CREATE_WAGER,
      payload: res.data,
    });

    dispatch(setAlert('wager created'));
  } catch (err) {
    const errors = err.response.data.errors;

    if (errors) {
      errors.forEach((error) => dispatch(setAlert(error.msg, 'danger')));
    }

    dispatch({
      type: WAGER_ERROR,
      payload: { msg: err.response.statusText, status: err.response.status },
    });
  }
};

